body {
  margin: 0;
}

#appContainer {
  background-color: #FFD086;
  min-height: 100vh;
  width: 100vw;
}

#chatBot {
  position: absolute;
  left: 20%;
  right: 20%;
  top: 3%;
  min-height: 60vh;
  width: 60vw;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  font-family: 'Source Sans Pro', sans-serif;
  background-color: white;
  border-radius: 20px;
  padding: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

h1 {
  text-shadow: 2px 3px 3px rgba(0,0,0,0.3);
}

input {
  width: 50%;
  margin-left: 40px;
  padding: 5px;
  border-radius: 3px;
  border: 1px solid transparent;
  border-top: none;
  border-bottom: 1px solid #DDD;
  box-shadow: inset 0 1px 2px rgba(0,0,0,.39), 0 -1px 1px #FFF, 0 1px 0 #FFF;
}

#botImage {
  height: 250px;
  width: auto;
  display: flex;
  margin: 0 auto 0 auto;
  border-radius: 50%;
}

#convoList {
  list-style-type: none;
  text-decoration: none;
  text-align: start;
}

.convoText {
  display: inline-flex;
  padding: 4px;
  border-radius: 15px;
  min-width: 40px;
  font-size: 14px;
}

@media only screen and (max-width: 600px) {
  #appContainer {
    height: 120vh;
  }
}